import style from './style.module.css';
import SmallStyledTextPink from '../SmallStyledTextPink/SmallStyledText';
import SmallStyledTextRed from '../SmallStyledTextRed/SmallStyledText';
import SmallStyledTextGreen from '../SmallStyledTextGreen/SmallStyledText';
import MobileStyledTextPink from '../MobileStyledTextPink/MobileStyledText';
import MobileStyledTextRed from '../MobileStyledTextRed/MobileStyledText';
import MobileStyledTextGreen from '../MobileStyledTextGreen/MobileStyledText';
import DarkCircle from './PinkPentagon.png';
import PurpleHexagon from './RedOentagon.png';
import YellowPentagon from './GreenPentagon.png';
import DarkPentagon from './DarkPentagon.png';
import PurpleCircle from './PurpleCircle.png';
import LightSquare from './LightSquare.png';
import { useMediaQuery } from 'react-responsive'




const Description = (props) =>{

    const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 1224 });
    if (isDesktopOrLaptop === true){
    return (
        <div>
            <div className = {style.desktopWrapper}>
                <div className = {style.geometryDecoration}>
                        <img className = {style.darkStarD} src = {DarkCircle} alt = 'Background  decoration'/>
                </div>
                <div className = {style.geometryDecoration2}>
                        <img className = {style.darkStar2D} src = {PurpleHexagon} alt = 'Background  decoration'/>
                </div>
                <div className = {style.geometryDecoration3}>
                        <img className = {style.darkStar3D} src = {YellowPentagon} alt = 'Background  decoration'/>
                </div>
                <div className = {style.geometryDecoration4}>
                        <img className = {style.darkStar4D} src = {DarkPentagon} alt = 'Background  decoration'/>
                </div>
                <div className = {style.geometryDecoration5}>
                        <img className = {style.darkStar5D} src = {PurpleCircle} alt = 'Background  decoration'/>
                </div>
                <div className = {style.geometryDecoration6}>
                        <img className = {style.darkStar6D} src = {LightSquare} alt = 'Background  decoration'/>
                </div>
                <div className = {style.firstItem}>
                        <div className = {style.customLabelDesktop}>
                            <SmallStyledTextPink text = {' '}/>
                        </div>
                        <h2 className = {style.description}>
                               - 
                            , , , , , k-pop,      
                             .
                        </h2>
                </div>
                <div className = {style.secondItem}>
                <div className = {style.customLabelDesktop}>
                            <SmallStyledTextRed text = {' '}/>
                        </div>
                        <h2 className = {style.description}>
                              ,   
                              .     - ,  ,
                               ,   ,    
                            .
                        </h2>
                    </div>
                    <div className = {style.thirdItem}>
                      
                        <div className = {style.customLabelDesktop}>
                            <SmallStyledTextGreen text = {' '}/>
                        </div>
                        <h2 className = {style.description}>
                                  ,  ,
                              :      .
                        </h2>
                    </div>
            </div>
        </div>
    );
    }else{
        return(
            <div>
          <div className = {style.mobileWrapper}>
          <div className = {style.geometryDecorationM}>
                        <img className = {style.darkStarM} src = {DarkCircle} alt = 'Background  decoration'/>
                </div>
                <div className = {style.geometryDecoration2M}>
                        <img className = {style.darkStar2M} src = {PurpleHexagon} alt = 'Background  decoration'/>
                </div>
                <div className = {style.geometryDecoration3M}>
                        <img className = {style.darkStar3M} src = {YellowPentagon} alt = 'Background  decoration'/>
                </div>
                   <div className = {style.geometryDecoration4M}>
                        <img className = {style.darkStar4} src = {DarkPentagon} alt = 'Background  decoration'/>
                </div>
                <div className = {style.geometryDecoration5M}>
                        <img className = {style.darkStar5} src = {PurpleCircle} alt = 'Background  decoration'/>
                </div>
                <div className = {style.geometryDecoration6M}>
                        <img className = {style.darkStar6} src = {LightSquare} alt = 'Background  decoration'/>
                </div>
                <div className = {style.firstItem}>
                        <div className = {style.customLabelMobile}>
                            <MobileStyledTextPink text = {' '}/>
                        </div>
                        <h2 className = {style.descriptionMobile}>
                               - 
                            , , , , , k-pop,      
                                 .
                        </h2>
                        </div>
                        <div className = {style.secondItem}>
                            <div className = {style.customLabelMobile}>
                                <MobileStyledTextRed text = {' '}/>
                            </div>
                            <h2 className = {style.descriptionMobile}>
                                  ,   
                                  .     - ,  ,
                                   ,   ,    
                                .
                            </h2>
                        </div>
                        <div className = {style.thirdItem}>
                            <div className = {style.customLabelMobile}>
                                <MobileStyledTextGreen text = {' '}/>
                            </div>
                            <h2 className = {style.descriptionMobile}>
                                      ,  ,
                                  :      .
                            </h2>
                        </div>
                    </div>
            </div>
        );
    }
}

export default Description;