import style from './style.module.css';
import { useMediaQuery } from 'react-responsive';
import DarkCircle from './DarkCircle.png';
import DarkPentagon from './DarkPentagon.png';
import DarkStar from './DarkStar.png';
const DirectMessageVidget = (props) =>{

    const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 1224 });

    if (isDesktopOrLaptop === true){
        if (props.time === 3){
            return (
                <div className = {style.desktopWrapper}>
                    <div className = {style.vidgetWrapperDesktop}>
                        <h2 className = {style.questionText3}> ?</h2>
                        <h2 className = {style.phoneIcon3}></h2>
                        <h2 className = {style.hotlineLabel}>  </h2>
                    </div>
                </div>
            );

        }else if (props.time === 2){
            return (
                <div className = {style.desktopWrapper}>
                    <div className = {style.vidgetWrapperDesktop}>
                        <h2 className = {style.questionText2}> ?</h2>
                        <h2 className = {style.phoneIcon2}></h2>
                        <h2 className = {style.hotlineLabel}>  </h2>
                    </div>
                </div>
            );
        }else if (props.time === 1){
            return (
                <div className = {style.desktopWrapper}>
                    <div className = {style.vidgetWrapperDesktop}>
                        <h2 className = {style.questionText1}> ?</h2>
                        <h2 className = {style.phoneIcon1}></h2>
                        <h2 className = {style.hotlineLabel}>  </h2>
                    </div>
                </div>
            );
        }else{
            return (
                <div className = {style.desktopWrapper}>
                   
                    <div className = {style.vidgetWrapperDesktop}>
                        <h2 className = {style.questionText0}> </h2>
                        <h2 className = {style.phoneIcon0}></h2>
                        <h2 className = {style.hotlineLabel}>  </h2>
                    </div>
                </div>
            );
        }
    }else{
        return (
            <div className = {style.mobileWrapper}>

            </div>
        );
    }
}

export default DirectMessageVidget;